* {
    font-family: Helvetica, sans-serif;
}

body {
    padding: 60px;
}

.titulo {
    text-align: center;
    font-size: 28px;
}

.card-grid {
    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
    width: 200px;
    height: 250px;
}

.card p {
    padding: 6px;
    text-align: center;
}

.card img {
    width: 100%;
    height: 150px;
    max-height: 180px;
}

.buscador {
    width: 100%;
    font-size: 1.2rem;
    border-radius: 10px;
    border: 1px solid rgb(0, 119, 255);
    outline: none;

}